/** @name Dependencies */
import React, { useEffect } from 'react';
import AOS from 'aos';
/** @name CSS */
import 'aos/dist/aos.css';

export const Fade = React.memo(({ children, animationName = "fade-down" }) => {
    useEffect(() => {
        AOS.init();
        window.addEventListener('scroll', () => AOS.refresh());
    },[])
    return (
        <div data-aos={animationName}>{children}</div>
    );
});