/** @name Dependencies */
import React, {useEffect, useRef, useState} from 'react';
/** @name Styled */
import { CenterContent, HighlightText } from '../../styled.main';
import { ElmHeader, ElmText, ElmVideoBackground, ElmImgBackground, ElmMouse, ElmScroll } from './styled';
/** @name Colors */
import { ORANGE_PRIMARY } from 'constants/colors';
/** @name External */
import { Render } from 'helpers';
/** @name Constants */
const IPAD_WIDTH = 1000;

export const Header = React.memo(() => {
    const [hasIpadWidth, setWindowWidth] = useState(window.innerWidth <= IPAD_WIDTH);
    const videoBackground = useRef(null);

    useEffect(() => {
        !hasIpadWidth && videoBackground.current.play()
        window.addEventListener("resize", () => setWindowWidth(window.innerWidth <= IPAD_WIDTH));
    }, [hasIpadWidth]);

    return (
        <ElmHeader id="header_fixed">
            <CenterContent>
                <ElmText>
                    não fique de fora,<br/>
                    a <HighlightText style={{ textShadow: 'rgb(255 91 0) 0px 3px 6px' }} color={ORANGE_PRIMARY}>inovação </HighlightText>
                    do <HighlightText style={{ textShadow: 'rgb(255 91 0) 0px 3px 6px' }} color={ORANGE_PRIMARY}>mercado de isp's</HighlightText><br/>
                    está por vir!
                </ElmText>
            </CenterContent>
            <Render condition={!hasIpadWidth}>
                <ElmVideoBackground ref={videoBackground}/>
            </Render>
            <Render condition={hasIpadWidth}>
                <ElmImgBackground/>
            </Render>
            <ElmMouse>
                <ElmScroll/>
            </ElmMouse>
        </ElmHeader>
    );
});