export class Events {

    /**
     *
     * @param id
     */
    static moveScrollTo(id){
        const elmBusinessModel = document.getElementById(id);
        return elmBusinessModel.scrollIntoView();
    };

    /**
     *
     * @param url
     * @returns {WindowProxy}
     */
    static openLink(url){
        return window.open(url);
    }
}